import React, { useState } from "react";
import styled from 'styled-components';
import { DragDropContext, Droppable } from 'react-beautiful-dnd';
import initialData from "../initial-data";
import Column from '../Column.jsx';
import '../style/Home.css';
import '../style/SetTimer.css';
import Dialog from "./DoneDialog";
import SetTimer from "./SetTimer";
import Fade from '@material-ui/core/Fade'
import { CommunicationSpeakerPhone } from "material-ui/svg-icons";
import wateringImg from './watering.png';
import windImg from './wind.png';
import trimmingImg from './trimming.png';

// Styles
const Container = styled.div`
  display: flex;
`;

// let flowers = [];
class Home extends React.Component {
  state = initialData; //initialDataÎäî ÏßÄÍ∏àÏùÄ Îî∞Î°ú ÌååÏùºÏóêÏÑú ÌïòÎìúÏΩîÎî©Ìïú ÏÉÅÌÉú. Ïù¥Ï†ú DBÏóêÏÑú Ïñ¥ÏºÄ Î∂ÄÎ•ºÏßÄ Î∞©Î≤ïÏùÑ Ï∞æÏïÑÎ≥¥Ïûê.

  // ÎìúÎûòÍ∑∏Í∞Ä ÏãúÏûëÎêòÎ©¥
  onDragStart = (startColumn, provided) => {
    document.body.style.transition = 'background-color 0.2s ease';
  }
  // ÎìúÎûòÍ∑∏Î•º Ìï¥ÏÑú ÏÉÅÌô©Ïù¥ ÏóÖÎç∞Ïù¥Ìä∏ÎêòÎ©¥
  onDragUpdate = update => {
    const { destination } = update;
    const opacity = destination ?
      destination.index / Object.keys(this.state.tasks).length
      : 0;
    document.body.style.backgroundColor = `rgba(153,  141, 217, ${opacity})`;
  }
  //Îî± ÎìúÎ°≠ÌñàÏùÑ ÎïåÍ∞Ä ÎêòÎ©¥
  onDragEnd = result => {
    document.body.style.color = 'inherit';
    document.body.style.backgroundColor = 'inherit';
    const { destination, source, draggableId, type } = result;

    // TaskÍ∞Ä ÏòÆÍ≤®Ï°åÏùÑ Îïå Ï≤òÎ¶¨ÌïòÍ∏∞
    if (!destination) { // 1. ÏïÑÏòà Ïù¥ÏÉÅÌïú Î∞îÍπ• Í≥≥Ïóê ÎÜìÏïòÏùÑ Í≤ΩÏö∞
      return;
    }
    if ( // 2. Ïù¥Ï†Ñ ÏúÑÏπòÏôÄ Í∞ôÏùÄ Í≥≥Ïóê ÎÜìÏïòÏùÑ Í≤ΩÏö∞
      destination.droppableId === source.droppableId &&
      destination.index === source.index
    ) {
      return;
    }

    // 3. TaskÍ∞Ä Done ÏπºÎüºÏúºÎ°ú ÏòÆÍ≤®Ï°åÏùÑ Í≤ΩÏö∞
    const rewardList = [".sunShineContainer", ".wateringContainer", ".windContainer", ".trimContainer"]
    const REWARD_COUNT = 4;
    if (destination.droppableId === 'column-3') {
      // (1) Ìï¥Îãπ TaskÎ•º disableDraggable ÌïúÎã§
      this.state.tasks[draggableId].isDone = true;

      // (2) ÌñáÎπõÏùÑ Ï™ºÏù∏Îã§
      const randomIndex = Math.floor(Math.random() * REWARD_COUNT);
      const container = rewardList[randomIndex];
      const randomReward = document.querySelector(container);
      randomReward.classList.add("showing");
      setTimeout(() => {
        randomReward.classList.remove("showing");
      }, 1000)
    }

    // Ïó¨Í∏∞ÏÑú Ïû†Íπê... Column ÏûêÏ≤¥Í∞Ä ÏòÆÍ≤®Ï°åÏùÑ Îïå Ï≤òÎ¶¨ÌïòÍ∏∞
    if (type === 'column') {
      const newColumnOrder = Array.from(this.state.columnOrder);
      newColumnOrder.splice(source.index, 1);
      newColumnOrder.splice(destination.index, 0, draggableId);

      const newState = {
        ...this.state,
        columnOrder: newColumnOrder
      };
      this.setState(newState);
      return;
    }

    const startColumn = this.state.columns[source.droppableId];
    const finishColumn = this.state.columns[destination.droppableId];
    
    // 4. persist reordering of task that has been moved within a list
    // 4. TaskÍ∞Ä Column ÎÇ¥ÏóêÏÑú ÏòÆÍ≤®Ï°åÏùÑ Îïå Í∑∏ ÏòÆÍ≤®ÏßÑ ÏûêÎ¶¨Í∞Ä Ïú†ÏßÄÎêòÎèÑÎ°ù Ï≤òÎ¶¨ÌïòÍ∏∞
    if (startColumn === finishColumn) {
      const newTaskIds = Array.from(startColumn.taskIds);
      newTaskIds.splice(source.index, 1); // Ïö∞ÏÑ† ÏòÆÍ≤®Ïßê ÎãπÌïú TaskÎ•º ÏõêÎûò ÏûêÎ¶¨ÏóêÏÑú ÏóÜÏï†Í≥†
      newTaskIds.splice(destination.index, 0, draggableId); // ÏòÆÍ∏¥ ÌõÑÏùò Í∑∏ ÏûêÎ¶¨Ïóê ÎÅºÏõåÎÑ£ÎäîÎã§

      const newColumn = { // Column ÎÇ¥ TaskÎì§ Í∞±Ïã†
        ...startColumn,
        taskIds: newTaskIds
      };
      const newState = { // Column Í∞±Ïã†
        ...this.state,
        columns: {
          ...this.state.columns,
          [newColumn.id]: newColumn
        }
      };

      this.setState(newState);
      return;
    }

    // 5. persist reordering of task that has been moved from one list to another
    // 5. TaskÍ∞Ä Column ÎÑàÎ®∏ ÏòÆÍ≤®Ï°åÏùÑ Îïå Í∑∏ ÏòÆÍ≤®ÏßÑ ÏûêÎ¶¨Í∞Ä Ïú†ÏßÄÎêòÎèÑÎ°ù Ï≤òÎ¶¨ÌïòÍ∏∞
    const startTaskIds = Array.from(startColumn.taskIds);
    startTaskIds.splice(source.index, 1);
    const newStart = {
      ...startColumn,
      taskIds: startTaskIds
    };
    const finishTaskIds = Array.from(finishColumn.taskIds);
    finishTaskIds.splice(destination.index, 0, draggableId);
    const newFinish = {
      ...finishColumn,
      taskIds: finishTaskIds
    };

    const newState = {
      ...this.state,
      columns: {
        ...this.state.columns,
        [newStart.id]: newStart,
        [newFinish.id]: newFinish,
      },
    }
    this.setState(newState)
    
    console.log("Todo is "+newState.columns["column-1"].taskIds);
    console.log("In Progress is "+newState.columns["column-2"].taskIds);
    
    // 6. TaskÍ∞Ä Column ÎÑàÎ®∏ ÏòÆÍ≤®Ï°åÏùÑ Îïå, Ïù¥Ï†ú ÎçîÏù¥ÏÉÅ To-do, In Progress ColumnÏóê ÏïÑÎ¨¥ Í≤ÉÎèÑ ÏóÜÏùÑ Í≤ΩÏö∞(Ï¶â Ï†ÑÎ∂Ä DoneÏπºÎüºÏúºÎ°ú ÏòÆÍ≤®Ï°åÏùÑ Í≤ΩÏö∞)
    const toDoIsNotEmpty = newState.columns["column-1"].taskIds.length;
    const InProgressIsNotEmpty = newState.columns["column-2"].taskIds.length;
    const FLOWERS = ["üåπ","üå∫","üåª","üåº","üå∑","üçÄ","üåµ"];
    const FLOWER_COUNT = 7;
    const LS_KEY_FLOWERS = "flowers";

    if (!toDoIsNotEmpty && !InProgressIsNotEmpty) {
      const randomIndex = Math.floor(Math.random() * FLOWER_COUNT);
      const theFlower = FLOWERS[randomIndex];
      alert(`Ï∂ïÌïòÌï©ÎãàÎã§!\nÍΩÉÏù¥ ÏûêÎûêÏñ¥Ïöî.\n${theFlower}`);

      let currentFlowersString = getFlower();
      if (!currentFlowersString){ // localÏóê ÏïÑÎ¨¥Í≤ÉÎèÑ ÏóÜÏùå
        currentFlowersString = theFlower;
      }
      else
      currentFlowersString += (","+theFlower);

      saveFlower(currentFlowersString);
    }
    function getFlower(){
      return localStorage.getItem(LS_KEY_FLOWERS);
    }
    function saveFlower(flowers){
      
      localStorage.setItem(LS_KEY_FLOWERS, flowers);
    }

    // TODO: Í∑∏Î¶¨Í≥† Ïù¥Î†áÍ≤å reorderÎêú indexÍ∞íÎì§ÏùÑ ÎîîÎπÑÏóê Ï†ÄÏû•Ìï¥Ïïº refreshÌñàÏùÑ ÎïåÎèÑ Ïú†ÏßÄÎê† Ïàò ÏûàÎäîÎç∞, Ïñ¥ÎñªÍ≤å ÌïòÏßÄ?
  };

  render() {
    return (
      <>
      <SetTimer
        toDoIsNotEmpty={this.state.columns["column-1"].taskIds.length}
        InProgressIsNotEmpty={this.state.columns["column-2"].taskIds.length}
      />
      
      <div className="sunShineContainer">
        <img src="https://lh3.googleusercontent.com/proxy/V7_ZR1X-TbB58fpWZCjUgnifEM2Ch4llYZiTUywXD83mVn1YiLHS7B0I0VrdVqEOZPsnXzJ4i3_m_bQWs4_ew6iFla0UJ0NO48WQE90coJkPIy0mwEct06AGgml55w" />
      </div>
      <div className="wateringContainer">
        <img src={wateringImg} />
      </div>
      <div className="windContainer">
        <img src={windImg} />
      </div>
      <div className="trimContainer">
        <img src={trimmingImg} />
      </div>
      <div className="budImageContainer">
        <img src="https://media1.giphy.com/media/1yTgqsdYPFw4Oqly5T/giphy.gif" />
      </div>
      {/* <div className="home"> */}
        <div className="toDoContainer">
          <DragDropContext
          onDragEnd={this.onDragEnd}
          onDragStart={this.onDragStart}
          onDragUpdate={this.onDragUpdate}
          >
            <Droppable
              droppableId="all-columns"
              direction="horizontal"
              type="column"
            >
              {provided => (
                <Container
                {...provided.droppableProps}
                ref={provided.innerRef}
                >
                  {this.state.columnOrder.map((columnId, index) => {
                    const column = this.state.columns[columnId];
                    const tasks = column.taskIds.map(taskId => this.state.tasks[taskId]);
                    return <Column key={column.id} column={column} tasks={tasks} index={index} />;
                  })}
                  {provided.placeholder}
                  
                </Container>
              )}
            </Droppable>
          </DragDropContext>
        </div>
        
      {/* </div> */}
      </>
    );
  }
}

export default Home;
