import { ImageExposureZero } from 'material-ui/svg-icons';
import React, { Component } from 'react';
import '../style/SetTimer.css';
import Home from "./Home";

let killCount = 0;
class SetTimer extends Component {
  constructor() {
    super();
    this.state = {
      hours: 0,
      minutes: 0,
      seconds:0
    }
    this.hoursInput = React.createRef();
    this.minutesInput= React.createRef();
    this.secondsInput = React.createRef();
  }

  inputHandler = (e) => {
    this.setState({[e.target.name]: e.target.value});
  }

  convertToSeconds = ( hours, minutes, seconds) => {
    return seconds + minutes * 60 + hours * 60 * 60;
  }

  startTimer = () => {
    this.timer = setInterval(this.countDown, 1000);
    // Ïù∏ÌíãÏóê Ï†ÅÏùÄ Ïà´Ïûê ÏßÄÏö∞Í∏∞
    const hoursInput = document.querySelector("input[name=hours]");
    const minutesInput = document.querySelector("input[name=minutes]");
    const secondsInput = document.querySelector("input[name=seconds]");
    const budContainer = document.querySelector(".budImageContainer");
    hoursInput.value="";
    minutesInput.value="";
    secondsInput.value="";
    budContainer.classList.add("showing");
  }

  countDown = () => {
    const  { hours, minutes, seconds } = this.state;
    const LS_KEY_KILL_PLANT = "kill_plant";
    killCount = JSON.parse(localStorage.getItem(LS_KEY_KILL_PLANT));
    let c_seconds = this.convertToSeconds(hours, minutes, seconds);
    if (!this.props.toDoIsNotEmpty && !this.props.InProgressIsNotEmpty){ // Ïö∞ÏÑ† Ìï†ÏùºÏùÑ Îã§ ÌïòÍ≤å ÎêòÎ©¥
      this.stopTimer();
    } else { // Ìï†Ïùº Îã§ Ïïà Ìïú Í≤ΩÏö∞Î©¥
      if ( hours === 0 &&  minutes === 0 & seconds === 0) { // time overÎùºÎ©¥
        killCount = JSON.parse(localStorage.getItem(LS_KEY_KILL_PLANT)) + 1;
        console.log("kill count is " + killCount);

        document.querySelector(".budImageContainer").classList.remove("showing");
        alert(`ÏãúÍ∞Ñ ÎÇ¥Ïóê Ìï†ÏùºÏùÑ Îã§ Î™ªÌïòÏÖ®Íµ∞Ïöî... ÍΩÉÏù¥ ÏãúÎì§ÏóàÏñ¥Ïöî. „Ö†„Ö†`)
      }
      localStorage.setItem(LS_KEY_KILL_PLANT, JSON.stringify(killCount));
    }
    
    if(c_seconds) {

      // seconds change
      seconds ? this.setState({seconds: seconds-1}) : this.setState({seconds: 59});

      // minutes change
      if(c_seconds % 60 === 0 && minutes) {
        this.setState({minutes: minutes -1});
      }

      // when only hours entered
      if(!minutes && hours) {
        this.setState({minutes: 59});
      }

      // hours change
      if(c_seconds % 3600 === 0 && hours) {
        this.setState({hours: hours-1});
      }

    } else {
      clearInterval(this.timer);
    } 
  }

  stopTimer = () => {
    clearInterval(this.timer);
    const hoursInput = document.querySelector("input[name=hours]");
    const minutesInput = document.querySelector("input[name=minutes]");
    const secondsInput = document.querySelector("input[name=seconds]");
    hoursInput.value="";
    minutesInput.value="";
    secondsInput.value="";
  }

  resetTimer = () => {
    this.setState({
      hours: 0,
      minutes: 0,
      seconds: 0
    });
    this.hoursInput.current.value = "";
    this.minutesInput.current.value = "";
    this.secondsInput.current.value = "";
  }

  render() {
    const { hours, minutes, seconds } = this.state;

    return (
      <div className="SetTimer">
        <h3>TIMER</h3>

        <div>
          <input ref={this.hoursInput} type="number" className="form-control input-lg" placeholder={"ÏãúÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"} name="hours" onChange={this.inputHandler} />
          <input ref={this.minutesInput} type="number" className="form-control input-lg" placeholder={"Î∂ÑÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"} name="minutes" onChange={this.inputHandler} />
          <input ref={this.secondsInput} type="number" className="form-control input-lg" placeholder={"Ï¥àÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"} name="seconds" onChange={this.inputHandler} />
        </div>
        
         <div>
            <button onClick={this.startTimer} className="startBtn">START</button>
            <button onClick={this.stopTimer}  className="stopBtn">STOP</button>
            <button onClick={this.resetTimer}  className="resetBtn">RESET</button>
         </div>

         <h4 className="timer-settimer">ÎÇ®ÏùÄ ÏãúÍ∞Ñ</h4>
         <h2>{hours<10? `0${hours}` : hours} : {minutes<10? `0${minutes}` : minutes} : {seconds<10? `0${seconds}` : seconds}</h2>
      </div>
    );
  }
}

export default SetTimer;